<template>
  <div class="my">
    <div class="my-o">
      <div class="my-box" :style="{backgroundImage: 'url(' + userInfo.userBg + ')'}">
        <div class="change-bg clearfix">
          <div class="fr cbg">
            <div>更换背景</div>
            <van-uploader class="upload-box" :max-size="maxSize" :after-read="uploadFile"/>
          </div>
        </div>
        <div class="user-info">
          <div class="clearfix">
            <div class="my-img fl" @click="Account">
              <img class="auto-img" :src="userInfo.userImg" alt />
            </div>
            <div class="nickname fl">{{userInfo.nickName}}</div>
          </div>
          <div class="text">简介：{{userInfo.desc == '' ? '这小家伙什么都没留下！' : userInfo.desc}}</div>
        </div>
      </div>
      <div class="content">
        <van-cell>
          <van-cell title="我的订单" icon="shop-o" @click="order">
            <!-- 使用 right-icon 插槽来自定义右侧图标 -->
            <template #right-icon class="dingdan">
              <van-icon name="arrow" style="line-height: inherit;" />
            </template>
          </van-cell>
          <div>
            <van-grid :border="false" :column-num="3">
              <van-grid-item icon="todo-list-o" text="待收货" :badge="waitshop.length ==0 ? '' : waitshop.length" @click="wait"/>
              <van-grid-item icon="logistics" text="已收货" :badge="received.length ==0 ? '' : received.length" @click="receive"/>
              <van-grid-item icon="records" text="待评论" />
            </van-grid>
          </div>
        </van-cell>
      </div>
      <div class="foot">
        <van-cell title="账号管理" icon="orders-o" @click="account">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon class="dingdan">
            <van-icon name="arrow" style="line-height: inherit;" />
          </template>
        </van-cell>
        <van-cell title="我的地址" icon="location-o" @click="location">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon class="dingdan">
            <van-icon name="arrow" style="line-height: inherit;" />
          </template>
        </van-cell>
        <van-cell title="联系我们" icon="points" @click="contact">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon class="dingdan">
            <van-icon name="arrow" style="line-height: inherit;" />
          </template>
        </van-cell>
        <van-cell title="客服与帮助" icon="phone-o" @click="contact">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon class="dingdan">
            <van-icon name="arrow" style="line-height: inherit;" />
          </template>
        </van-cell>
        <van-cell title="安全中心" icon="hotel-o" @click="secure">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon class="dingdan">
            <van-icon name="arrow" style="line-height: inherit;" />
          </template>
        </van-cell>
      </div>
    </div>
  </div>
</template>
<script>

//命名空间用法
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("shopcart");

export default {
  name: "My",
  data() {
    return {
      //用户信息
      userInfo: {},
      maxSize:2 * 1024 * 1024
    };
  },

  created() {
    this.getUserInfo();
  },
  computed: {
    ...mapState(["waitshop","received"])
  },
  methods: {
    //获取用户信息
    getUserInfo() {
      //订单编号
      let tokenString = localStorage.getItem("_TNK");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //更换背景
    uploadFile(file){
      //获取图片base64
      let serverBase64Img = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, '');

      //获取图片类型
      let imgType = file.file.type.split('/')[1];

      let tokenString = localStorage.getItem("_TNK");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img,
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'I001') {
            this.userInfo.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //跳转到订单
    order() {
      this.$router.push({ name: "Order" });
    },

    //账号管理
    account(){
      this.$router.push({name:"Account"})
    },
    Account(){
      this.$router.push({name:"Account"})
    },

    //安全中心
    secure(){
      this.$router.push({name:"Secure"})
    },

    //联系我们
    contact(){
      this.$router.push({name:"Contact"})
    },

    //跳转到地址页面
    location(){
      this.$router.push({name:"MyAddress"})
    },



    //跳转到待收货
    wait(){
      this.$router.push({name:"Wait"})
    },

    //跳转到已收货
    receive(){
      this.$router.push({name:"Received"})
    }
  }
};
</script>
<style lang="less" scoped>
.my {
  .my-nar {
    /deep/ .van-icon {
      font-size: 24px !important;
      color: #999999;
    }
  }
  .my-o {
    .my-box {
      background-color: #666;
      padding: 20px;
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: center center;
    }
    .change-bg,
    .text {
      font-size: 14px;
      color: #fff;
      height: 30px;
      line-height: 30px;
    }
    .my-img {
      background-color: #fff;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.4);
      overflow: hidden;
    }
    .nickname {
      margin-left: 20px;
      line-height: 60px;
      color: #fff;
      font-size: 14px;
    }
    .text {
      margin-top: 10px;
      margin-left: 80px;
    }
    .cbg {
      position: relative;
    }
    .upload-box {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 30px;
      overflow: hidden;
      opacity: 0;
    }
    .my-name {
      width: 100%;
      text-align: center;
      position: absolute;
      color: #ffffff;
      font-size: 18px;
      bottom: 26px;
    }
    .content {
      margin-bottom: 10px;
      /deep/ .van-cell {
        padding: 8px 8px;
        font-size: 16px;
      }
      /deep/ .van-cell__left-icon {
        font-size: 30px;
        color: salmon;
      }
    }
    .foot {
      /deep/ .van-cell {
        padding: 16px 8px;
        font-size: 16px;
      }
      /deep/ .van-cell__left-icon {
        font-size: 22px;
      }
    }
  }
}
</style>